body {
  font-family: 'Open Sans', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Playfair Display', serif;
}

a {
  color: var(--primary);
}

a:hover {
  color: var(--secondary);
}


.display-4 {
  font-size: 2.5rem;
}

.card-img-right {
  height: 100%;
  border-radius: 0 3px 3px 0;
}

footer {
  border-top: .05rem solid #e5e5e5;
}

.nav-item.active>a {
  border-top-left-radius: .125rem !important;
  border-top-right-radius: .125rem !important;
  border-bottom: .214rem solid var(--blue-light) !important;
}

#separatorItem:hover,
#separatorLink:hover {
  border: none;
}

.navbar {
  z-index: 99;
}

.illustrations-step {
  height: 250px;
}

.sticky-offset {
  top: 95px;
  z-index: 2;
}

#sidebar-wrapper {
  position: relative;
  min-height: 100vh;
  margin-left: -15rem;
  display: none;
  -webkit-transition: margin .25s ease-out;
  -moz-transition: margin .25s ease-out;
  -o-transition: margin .25s ease-out;
  transition: margin .25s ease-out;
}

#sidebar-wrapper .sidebar-heading {
  padding: 0.875rem 1.25rem;
  font-size: 1.2rem;
}

#sidebar-wrapper .sidebar-foote {
  padding: 0.875rem 1.25rem;
  font-size: 1.2rem;
}

#sidebar-wrapper .list-group {
  width: 15rem;
  padding: 0;
  margin: 0;
}

#wrapper.toggled #sidebar-wrapper {
  /* margin-left: 0; */
  display: block;
}

#page-content-wrapper {
  /* min-width: 100%; */
  width: 100%;
}

@media (min-width: 992px) {
  #sidebar-wrapper {
      margin-left: 0;
      display: block;
  }

  #page-content-wrapper {
      min-width: 0;
      width: 100%;
  }

  #wrapper.toggled #sidebar-wrapper {
      /* margin-left: -15rem; */
      display: block;
  }
}

#menu-toggle {
  color: var(--dark);
  padding: .25rem .75rem;
  font-size: 1.25rem;
  border: 1px solid rgba(0,0,0,.1);
  border-radius: .125rem;
  background: transparent;
  cursor: pointer;
  box-shadow: 0 1px 1px 0 rgba(0,0,0,.14);
}

#v-pills-tabContent h1,
#v-pills-tabContent h2,
#v-pills-tabContent h3 {
  font-family: 'Open Sans', sans-serif;
  margin: 50px 0;
  color: white;
  background: var(--blue-light);
  border-radius: 15px;
  padding: 20px;
}

#v-pills-tabContent h2 {
  margin: 20px 0;
}


#v-pills-tabContent ol,
#v-pills-tabContent ul {
  list-style: decimal;
  margin-left: 40px;
}

.list-group-item {
  color: rgba(0, 0, 0, .5);
}

.github-link {
  /* color: rgba(0, 0, 0, .5); */
  color: var(--blue-light);
  font-size: 0.9rem;
  text-decoration: none;
}

.list-group-item.active {
  color: rgba(0, 0, 0, .9);
  border-top-left-radius: .125rem !important;
  border-top-right-radius: .125rem !important;
  border-bottom: .214rem solid var(--blue-light) !important;
}

.avatar-card-container {
  margin-bottom: 100px;
}

.avatar-card {
  padding-top: 100px;
}

.fonctions {
  padding: 5px 20px;
}

.avatar-img-container {
  z-index: 3;
  border-radius: 50%;
  position: absolute;
  top: -5px;
  left: 50%;
  width: 200px;
  height: 200px;
  transform: translate(-50%, -50%);

  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.avatar-placeholder-container {
  z-index: 3;
  border-radius: 50%;
  position: absolute;
  top: -5px;
  left: 50%;
  width: 200px;
  height: 200px;
  transform: translate(-50%, -50%);
  background: var(--orange);
}

.avatar-placeholder-container p {
  font-family: 'Playfair Display', serif;
  font-size: 4rem;
}

.card-style1 {
  border: 2px solid var(--red);
}

.card-style2 {
  border: 2px solid var(--purple);
}

.badges {
  height: 32px;
  width: 32px;
  margin-right: 5px;
}

#formSoutien {
  height:1800px;
  width:60%;
  border:none;
}


@media (max-width: 1000px) {
  #formSoutien {
      width:90%;
  }
}

@media (max-width: 575.98px) {
  h1,h2,h3 {
    font-size: 24px;
  }
  body {
      padding-top: 4rem;
  }
}